import React from 'react';
import { Navbar, Container, Nav} from 'react-bootstrap';
import { Link, useNavigate } from 'react-router-dom';
import Modal1 from '../Components/Modal1';

function Header() {
  const [isLogin, setIsLogin] = React.useState(false);
  const navigate = useNavigate();
  const showAlert = (message) => {
    alert(message);
  }

  React.useEffect(() => {
    let token = localStorage.getItem('login-token');
    if (token) {
      setIsLogin(true);
    } else {
      setIsLogin(false);
    }
  }, []);

  const handleLogout = () => {
    localStorage.removeItem('login-token');
    setIsLogin(false);
    navigate('/');
  };
  
  return (
    

    <div className="Header">
      <div className="Header1">
        <div className="nav_logo">
          <Navbar bg="primary" data-bs-theme="dark" className="Header2">
            <Container>
              <Navbar.Brand href="/" className="Header3">
                WON
              </Navbar.Brand>
              <Nav className="me-auto">
                <Nav.Link as={Link} to="/credit"></Nav.Link>
                <Nav.Link onClick={() => showAlert("    .")}></Nav.Link>
                <Nav.Link onClick={() => showAlert("    .")}></Nav.Link>
                <Nav.Link  onClick={() => showAlert("    .")}></Nav.Link>

              </Nav>
              <form className="nav justify-content-end">
                {isLogin ? (
                  <>
                    <Nav.Link as={Link} to="/first"></Nav.Link>
                    <Nav.Link onClick={handleLogout}></Nav.Link>
                    <div><Modal1></Modal1></div> 
                  </>
                ) : (
                  <>
                    <Nav.Link as={Link} to="/signup"></Nav.Link>
                    <Nav.Link as={Link} to="/login"></Nav.Link>
                    <div><Modal1></Modal1></div> 
                  </>
                )}
              </form>
            </Container>
          </Navbar>
    </div>
  </div>
</div>
  );
}

export default Header;