import React from 'react';
import './css/Credit.css';
import Detail from './Detail.js';

function CardList(props) {
  return (
    <div className="col-md-3">
      <div className='card_list'>
        <img src={props.card.card_image} alt={props.card.cardName} height="250px" width="158px" />
        <h4 className="card1">{props.card.cardName}</h4>
        <p className='card2'>: {props.card.cardCorp}</p>
        {/* <p>: {props.card.domesticFee}</p> */}
        {/* <p>: {props.card.overseasFee}</p> */}
        {/* <p>  : {props.card.overseasYn ? "" : ""}</p> */}
        {/* <p>: {props.card.minLastMonth}</p> */}
        {/* <p>: {props.card.minThisMonth}</p> */}
        <p className="card3">: {props.card.benefitMain}</p>
        {/* <p>: {props.card.benefitMate}</p> */}
        {/* <p>: {props.card.cardUrl}</p> */}
        <Detail card={props.card}></Detail>
      </div>
    </div>
  );
}

export default CardList;
