import React, { useState } from "react";
import { useForm } from "react-hook-form";
import axios from "axios";
import "./css/Signup.css";
import Modal from 'react-bootstrap/Modal';

const Signup = () => {
  const { register, handleSubmit, formState: { errors }, reset } = useForm({ mode: 'onChange' });
  const [isUserIdAvailable, setUserIdAvailable] = useState(true);
  const [showSuccessModal, setShowSuccessModal] = useState(false);
  const [showErrorModal, setShowErrorModal] = useState(false);
  
  const handleCloseSuccessModal = () => {
    setShowSuccessModal(false);
    document.location.href = '/login';
  };

  const handleCloseErrorModal = () => {
    setShowErrorModal(false);
  };

  const onSubmit = async ({ userId, userPw, userName, userBirth, phoneNumber, gender }) => {
    try {
      const response = await axios.post(
        "http://localhost:8080/auth/signup",
        {
          userId:userId,
          password:userPw,
          username:userName,
          phoneNumber:phoneNumber,
          birthday:userBirth,
          gender: gender,
        },
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      );
  
      console.log(" .", response.data);
      setShowSuccessModal(true);
    } catch (error) {
      console.error(" .   .", error);
      setShowErrorModal(true);
      if (error.response) {
        console.log('Error response:', error.response); 
        if (error.response.data === 'Duplicate ID' || error.response.status === 400) {
          setShowErrorModal(true);
        }
      }
    }
  }


  return (
    <div>
    <section className="col-4 offset-md-4 signup2">
      <div className="signup-card">
      <div className="signup6">  </div>
      <p className="signup5"> </p>
        <form onSubmit={handleSubmit(onSubmit)} className="signup4">
  
          <div className="mb-3">
            <label htmlFor="userId" className="signup11">
              
            </label>
            <input
              type="text"
              id="userId"
              className={`signup10 ${errors.userId || !isUserIdAvailable ? 'is-invalid' : ''}`}
              {...register("userId", {
                required: "    ",
                pattern: {
                  value: /^[A-Za-z0-9]{2,12}$/,
                  message: "   2~12 .",
                },
              })}
              placeholder=" "
            />
            {!isUserIdAvailable && (
              <div className="signup3">   .</div>
            )}
            {errors.userId && (
              <div className="signup3">{errors.userId.message}</div>
            )}
          </div>


          <div className="mb-3">
            <label htmlFor="userName" className="signup11"></label>
            <input
              type="text"
              id="userName"
              className={`signup10 ${errors.userName ? 'is-invalid' : ''}`}
              {...register('userName', {
                required: '    ',
                minLength: {
                  value: 2,
                  message: '2  .'
                }
              })}
                placeholder=" "
            />
            {errors.userName &&
              <div className="signup3">
                {errors.userName.message}
              </div>
            }
          </div>

          <div className="mb-3">
            <label htmlFor="userPw" className="signup11"></label>
            <input
              type="password"
              id="userPw"
              className={`signup10 ${errors.userPw ? 'is-invalid' : ''}`}
              {...register('userPw', {
                required: '   ',
                pattern: {
                  value: /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,16}$/,
                  message: ', ,   8~16 .'
                }
              })}
              placeholder=" "
            />
            {errors.userPw &&
              <div className="signup3">
                {errors.userPw.message}
              </div>
            }
          </div>

          <div className="mb-3">
            <label htmlFor="phoneNumber" className="signup11"></label>
            <input
              type="text"
              id="phoneNumber"
              className={`signup10 ${errors.phoneNumber ? 'is-invalid' : ''}`}
              {...register('phoneNumber', {
                required: '     .',
                pattern: {
                  value: /^[0-9]{10,11}$/,
                  message: '- .'
                }
              })}
                placeholder= " "
            />
            {errors.phoneNumber &&
              <div className="signup3">
                {errors.phoneNumber.message}
              </div>
            }
          </div>


          <div className="mb-3">
            <label htmlFor="userBirth" className="signup11"></label>
            <input
              type="text"
              id="userBirth"
              className={`signup10 ${errors.userBirth ? 'is-invalid' : ''}`}
              {...register('userBirth', {
                required: '    ',
                pattern: {
                  value: /^[0-9]{8}$/,
                  message: ' 8 .'
                }
              })}
                placeholder=" "
            />
            {errors.userBirth &&
              <div className="signup3">
                {errors.userBirth.message}
              </div>
            }
          </div>

          <div className="mb-3">
            <label htmlFor="gender" className="signup11"></label>
            <div>
              <div className="signup9">
              <label>
                <input
                  type="radio"
                  value="male"
                  {...register('gender', {
                    required: ' .'
                  })}
                /> 
              </label><br></br>
              <label >
                <input
                  type="radio"
                  value="female" 
                  {...register('gender', {
                    required: ' .'
                  })}
                /> 
              </label>
              </div>
            </div>
            {errors.gender &&
              <div className="signup3">
                {errors.gender.message}
              </div>
            }
          </div>
            
          <div className="mb-3">
            <label className="form-check-label">
              <input
                type="checkbox"
                className="form-check-input"
                {...register("agreement", { required: "   ." })}
              />
                   .
              {errors.agreement && (
                <div className="signup3">{errors.agreement.message}</div>
              )}
            </label>
          </div>

          <div className="text-center">
            <button
              type="submit"
              className="signup7"
            >
               
            </button>
          </div>
            <br/><br/>
        </form>
      </div>
    </section>
    <Modal show={showSuccessModal} onHide={handleCloseSuccessModal} >
        <Modal.Header closeButton>
          <Modal.Title> </Modal.Title>
        </Modal.Header>
        <Modal.Body> .</Modal.Body>
        <Modal.Footer>
          <button onClick={handleCloseSuccessModal}></button>
        </Modal.Footer>
      </Modal>

      <Modal show={showErrorModal} onHide={handleCloseErrorModal}>
        <Modal.Header closeButton>
          <Modal.Title> </Modal.Title>
        </Modal.Header>
        <Modal.Body>
              .
          <br/> 
           ID .
          </Modal.Body>
        <Modal.Footer>
          <button onClick={handleCloseErrorModal}></button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default Signup;
