import { useState } from 'react';
import { Modal, Button} from 'react-bootstrap';
import './css/Detail.css';

function Detail({ card }) {  // card  props .
  const [showModal, setShowModal] = useState(false);

  const handleCloseModal = () => setShowModal(false);
  const handleShowModal = () => setShowModal(true);

  return (
    <>
      <button className='btn btn-outline-primary' onClick={handleShowModal}> </button>
      <Modal show={showModal} onHide={handleCloseModal} size="lg">
        
        <Modal.Header className='modal_header' closeButton>
          <Modal.Title className='detail5'>  </Modal.Title>
        </Modal.Header>
        <div className='detail1'>
        <img src={card.card_image} alt={card.cardName} height="250px" width="158px"/>
        </div>
        <Modal.Body className='modal_body'>
        <h4 className='detail2'>{card.cardName}</h4>  
        <div className='detail3'>
          <p>: {card.domesticFee}</p>
          <p>: {card.overseasFee}</p>
          <p>: {card.minLastMonth}</p>
          <p>: {card.minThisMonth}</p>
          <p>: {card.benefitMate}</p>
          <p> : {card.benefitMain}</p>
        </div>
        <div className='detail4'>
        <button className='btn btn-primary' onClick={() => window.open(card.cardUrl, '_blank')}> </button>
        </div>
        </Modal.Body>
      </Modal>
    </>
  );
}

export default Detail;