import '../App.css';
import {Card, Button, Carousel } from 'react-bootstrap';
import { Routes, Route} from 'react-router-dom';
import Credit from './Credit';
import Modal2 from '../Components/Modal2';
function Home() {
  return (
    <div>
      <Routes>
        <Route
          path="/"
          element={
            <>
              <br />
              <div className = "banner">
                <Carousel className="carousel-container">
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src=".\images\slider01.jpg"
                      alt="First slide"
                    />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src=".\images\slider02.jpg"
                      alt="Second slide"
                    />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src=".\images\slider04.jpg"
                      alt="Second slide"
                    />
                  </Carousel.Item>
                </Carousel>
              </div>
              <br />
              <div className="card-parent">
                <Card style={{ width: '36rem' }}>
                  <Card.Img
                    variant="top"
                    src="./images/recom_image2.jpg"
                    height="270"
                  />
                  <Card.Body>
                    <Card.Text>
                      <div className='home1'>                             <br />
                         ?
                      </div></Card.Text>
                    <Button href="/credit">  </Button>
                  
                  </Card.Body>
                </Card>
                <Card style={{ width: '36rem' }}>
                  <Card.Img
                    variant="top"
                    src="./images/recom_image1.jpg"
                    height="270"
                  />
                  <Card.Body>
                    <Card.Text>
                    <div className='home1'>
                            <br />
                        
                      </div></Card.Text>
                    <Modal2></Modal2>
                  </Card.Body>
                </Card>
              </div>
              <br/><br/>
            </>
          }
        />
        <Route path="/credit" element={<Credit />} />
        {/* //     */}
        {/*          */}
        {/* : <Route path="/credit_modal" element={<CreditModal />} /> */}
      </Routes>
      </div>
  );
}

export default Home;