import React, { useState } from 'react';
import StepWizard from 'react-step-wizard';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';
import './css/Modal2.css';
import { useNavigate } from "react-router-dom";

function Modal2() {
  const navigate = useNavigate();
  const [card_own_yn, setCard_own_yn] = useState('');
  const [card_purpose, setCard_purpose] = useState('');
  const [prefer_benefit, setPrefer_benefit] = useState('');
  const [most_benefit, setMost_benefit] = useState('');
  const [airport_mileage, setAirport_mileage] = useState('');
  const [prefer_design, setPrefer_design] = useState('');
  const [card_year_fee, setCard_year_fee] = useState('');
  const [card_monthly_fee, setCard_monthly_fee] = useState('');
  const [monthly_expense, setMonthly_expense] = useState('');
  const [step, setStep] = useState(1);

  const handlePrevClick = () => {
    if (step > 1) {
      setStep(step - 1);
    }
  };  
  
  const handleNextClick = () => {
    if (step > 1) {
      setStep(step + 1);
    }
  }; 

  const handleFirstClick = () => {
    if (card_own_yn === 'yes') {
      setStep(6);
    } else if (card_own_yn === 'no') {
      setStep(2);
    }
  };
  

  const [show, setShow] = useState(false);
  const [loading, setLoading] = useState(false);
  const [darkenBackground, setDarkenBackground] = useState(false); 

  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log("Form Submitted");
    setLoading(true);
    setDarkenBackground(true);

    await new Promise(res => setTimeout(res, 2000));

    setLoading(false);
    navigate("/credit/recommend");
    setShow(false);
    setDarkenBackground(false);
  };

  return (
    <>
      <Button variant="primary" onClick={() => setShow(true)}>
          
      </Button>
      <div className={`modal-overlay ${darkenBackground ? 'darken' : ''}`}>
      <Modal show={show} onHide={() => setShow(false)} className="modal2_1" aria-labelledby="example-custom-modal-styling-title" centered={true}>
      <Modal.Header closeButton>
        <br/>
        <div className="modalfont1">
            
          </div>
      </Modal.Header>
        <br/>
        <Modal.Body style={{ overflowY: 'auto', maxHeight: '700px', maxWidth:'1200px'}}>
          <p className='modalfont2'>
                   .
          </p>
          <hr></hr>
          <Form onSubmit={handleSubmit}>
          
          {step === 1 && (
            <Form.Group className='modalfont3'>
              <Form.Label>1.    ?</Form.Label>
              <Form.Check
                type="radio"
                label=""
                name="card_own_yn"
                value="yes"
                onChange={(e) => {setCard_own_yn(e.target.value);}}
              />
              <Form.Check
                type="radio"
                label=""
                name="card_own_yn"
                value="no"
                onChange={(e) => {setCard_own_yn(e.target.value);}}
              />
              <div className="modalButtonContainer">
                <Button className="modalButton" type="button" onClick={handleFirstClick}></Button>
              </div>
            </Form.Group>
          )}

          
          { card_own_yn === "no" && step === 2 && ( 
          <Form.Group className='modalfont3'>
            <Form.Label>2.    ?</Form.Label>
            <Form.Check 
              type="radio" 
              label="  " 
              name="card_purpose" 
              value="for_benefits"
              onChange={(e) => setCard_purpose(e.target.value)}
            />
            <Form.Check 
              type="radio" 
              label=" " 
              name="card_purpose" 
              value="installment"
              onChange={(e) => setCard_purpose(e.target.value)}
            />
            <Form.Check 
              type="radio" 
              label=" " 
              name="card_purpose" 
              value="tax_deduction"
              onChange={(e) => setCard_purpose(e.target.value)}
            />
            <Form.Check 
              type="radio" 
              label="" 
              name="card_purpose" 
              value="others"
              onChange={(e) => setCard_purpose(e.target.value)}
            />
            <div className="modalButtonContainer">
                <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
                <Button className="modalButton" type="button" onClick={handleNextClick}></Button>
              </div>
          </Form.Group>
          )}
          
            {step === 3 && (
          <Form.Group className='modalfont3'>
            <Form.Label>3.   ,    ?</Form.Label>
            <Form.Check 
              type="radio" 
              label="(/)" 
              name="prefer_benefit" 
              value="discounts_rewards"
              onChange={(e) => {setPrefer_benefit(e.target.value); setStep(3);}}
            />
            <Form.Check 
              type="radio" 
              label="" 
              name="prefer_benefit" 
              value="air_miles"
              onChange={(e) => {setPrefer_benefit(e.target.value); setStep(3);}}
            />
            <Form.Check 
              type="radio" 
              label="" 
              name="prefer_benefit" 
              value="design"
              onChange={(e) => {setPrefer_benefit(e.target.value); setStep(3);}}
            />
            <Form.Check 
              type="radio" 
              label="" 
              name="prefer_benefit" 
              value="annual_fee"
              onChange={(e) => {setPrefer_benefit(e.target.value); setStep(3);}}
            />
            <Form.Check 
              type="radio" 
              label=" " 
              name="prefer_benefit" 
              value="previous_month_performance"
              onChange={(e) => {setPrefer_benefit(e.target.value); setStep(3);}}
            />
              <div className="modalButtonContainer">
                <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
                <Button className="modalButton" type="button" onClick={handleNextClick}></Button>
              </div>
          </Form.Group>
          )} 
          

          { prefer_benefit === 'discounts_rewards' && step === 4 &&(
            <Form.Group className='modalfont3'>
              <Form.Label>4.   ,     ?</Form.Label>
              <Form.Check type="radio" label="" name="most_benefit"/>
              <Form.Check type="radio" label="" name="most_benefit"/>
              <Form.Check type="radio" label="" name="most_benefit"/>
              <Form.Check type="radio" label="" name="most_benefit"/>
              <Form.Check type="radio" label="" name="most_benefit"/>
              <Form.Check type="radio" label="" name="most_benefit"/>
              <Form.Check type="radio" label="" name="most_benefit"/>
              <Form.Check type="radio" label="" name="most_benefit"/>
              <Form.Check type="radio" label="" name="most_benefit"/>
              <Form.Check type="radio" label=" " name="most_benefit"/>
              <Form.Check type="radio" label=" " name="most_benefit"/>
              <div className="modalButtonContainer">
                <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
                <Button className="modalButton" type="button" onClick={handleNextClick}></Button>
              </div>
            </Form.Group>
            )}

          { prefer_benefit === 'air_miles' && step === 4 &&( 
            <Form.Group className='modalfont3'>
              <Form.Label>4.   ,     ?</Form.Label>
              <Form.Check type="radio" label="" name="airport_mileage" />
              <Form.Check type="radio" label="" name="airport_mileage" />
              <Form.Check type="radio" label="" name="airport_mileage" />
              <Form.Check type="radio" label=" " name="airport_mileage" />
              <div className="modalButtonContainer">
                <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
                <Button className="modalButton" type="button" onClick={handleNextClick}></Button>
              </div>
            </Form.Group>
            )}

          { prefer_benefit === 'design' && step === 4 &&(
            <Form.Group className='modalfont3'>
              <Form.Label>4.   ,    ?</Form.Label>
              <Form.Check type="radio" label="" name="prefer_design" />
              <Form.Check type="radio" label="" name="prefer_design" />
              <Form.Check type="radio" label=" " name="prefer_design" />
              <Form.Check type="radio" label="" name="prefer_design" />
              <Form.Check type="radio" label="" name="prefer_design" />
              <div className="modalButtonContainer">
                <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
                <Button className="modalButton" type="button" onClick={handleNextClick}></Button>
              </div>
            </Form.Group>
            )}

          { prefer_benefit === 'annual_fee' && step === 4 &&(
            <Form.Group className='modalfont3'>
              <Form.Label>4.   ,    ?</Form.Label>
              <Form.Check type="radio" label=" " name="card_year_fee" />
              <Form.Check type="radio" label="3 " name="card_year_fee" />
              <Form.Check type="radio" label="5 " name="card_year_fee" />
              <Form.Check type="radio" label=" " name="card_year_fee" />
              <div className="modalButtonContainer">
                <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
                <Button className="modalButton" type="button" onClick={handleNextClick}></Button>
              </div>
            </Form.Group>
            )}

          { prefer_benefit === 'previous_month_performance' && step === 4 &&(
            <Form.Group className='modalfont3'>
              <Form.Label>4.   ,     ?</Form.Label>
              <Form.Check type="radio" label="" name="card_monthly_fee" />
              <Form.Check type="radio" label="30 " name="card_monthly_fee" />
              <Form.Check type="radio" label="40 " name="card_monthly_fee" />
              <Form.Check type="radio" label="50 " name="card_monthly_fee" />
              <Form.Check type="radio" label=" " name="card_monthly_fee" />
              <div className="modalButtonContainer">
                <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
                <Button className="modalButton" type="button" onClick={handleNextClick}></Button>
              </div>
            </Form.Group>
          )}
             { step === 5 &&(
            <Form.Group className='modalfont3'>
              <Form.Label>5.      ,    .</Form.Label>
              <Form.Check 
                type="radio" 
                label="30 " 
                name="monthlyExpense" 
                value="under_300k"
                onChange={(e) => setMonthly_expense(e.target.value)}
              />
              <Form.Check 
                type="radio" 
                label="50 " 
                name="monthlyExpense" 
                value="under_500k"
                onChange={(e) => setMonthly_expense(e.target.value)}
              />
              <Form.Check 
                type="radio" 
                label="70 " 
                name="monthlyExpense" 
                value="under_700k"
                onChange={(e) => setMonthly_expense(e.target.value)}
              />
              <Form.Check 
                type="radio" 
                label="100 " 
                name="monthlyExpense" 
                value="under_1m"
                onChange={(e) => setMonthly_expense(e.target.value)}
              />
              <Form.Check 
                type="radio" 
                label="100 " 
                name="monthlyExpense" 
                value="over_1m"
                onChange={(e) => setMonthly_expense(e.target.value)}
              />
            <div className="modalButtonContainer">
              <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
              <Button className="modalButton" type="submit"></Button>
            </div>
            </Form.Group>
            )}

            { card_own_yn === "yes" && step === 6 && (
            <>
            <Form.Group className='modalfont3'>
              <Form.Label>2.        .</Form.Label>
              <Form.Control
                type="number"
                placeholder="  "
                onChange={(e) => setMonthly_expense(e.target.value)}
              />
              <div className="modalButtonContainer">
                <Button className="modalButton" type="button" onClick={handlePrevClick}></Button>
              <Button className="modalButton" type="submit"></Button>
            </div>
            </Form.Group>
            </>
            )}
          </Form>
        </Modal.Body>
      </Modal>
      </div>
      
      <Modal show={loading} centered={true} className="loadingModal">
        <Modal.Body className="Modal__Body">
            <div className="title">WON</div>
            <div className="info">    .<br/>   .</div>
            <div className="note">     N    .</div>
        </Modal.Body>
        </Modal>
    </>
  );
}

export default Modal2;