import React, { useState, useEffect } from 'react';
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import First from './First';

import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import './css/Recommend.css';
import Star from '../Components/Star';
import { useNavigate } from 'react-router-dom';

function Recommend(props) {
  const [showModal, setShowModal] = useState(false);
  const navigate = useNavigate();
  const handleGoHome = () => {
    navigate('/');
  };

  const handleCloseModal = () => setShowModal(false);
  const handleShowModal = () => setShowModal(true);


  const cards = [
    {
        card_no: 1,
        card_corp: '',
        card_name: '_taptap_O',
        domestic_fee: 10000,
        overseas_yn: 1,
        overseas_fee: 10000,
        min_last_month: 300000,
        min_this_month: 0,
        benefit_main: '',
        benefit_cate: ',/,/,,',
        card_url: 'https://www.samsungcard.com/home/card/cardinfo/PGHPPCCCardCardinfoDetails001?code=AAP1483',
        card_image: 'https://static11.samsungcard.com/wcms/home/scard/image/personal/b_AAP1483.png'
    },
    {
        card_no: 2,
        card_corp: '',
        card_name: '_&_MILEAGE_PLATINUM()',
        domestic_fee: 47000,
        overseas_yn: 1,
        overseas_fee: 49000,
        min_last_month: 0,
        min_this_month: 0,
        benefit_main: '',
        benefit_cate: ',,,,/',
        card_url: 'https://www.samsungcard.com/home/card/cardinfo/PGHPPCCCardCardinfoDetails001?code=AAP1452',
        card_image: 'https://static11.samsungcard.com/wcms/home/scard/image/personal/b_AAP1452_03.png'
    },
    {
        card_no: 10,
        card_corp: '',
        card_name: '_iD_PET_',
        domestic_fee: 15000,
        overseas_yn: 1,
        overseas_fee: 15000,
        min_last_month: 400000,
        min_this_month: 0,
        benefit_main: '',
        benefit_cate: ',,,,',
        card_url: 'https://www.samsungcard.com/home/card/cardinfo/PGHPPCCCardCardinfoDetails001?code=AAP1773',
        card_image: 'https://vertical.pstatic.net/vertical-cardad/creatives/SS/10184/SS_10184_20221004-224031_ver.png'
    }
];

    return (
      <div className="main-container">
        <h2 className="recommended-title">  {localStorage.getItem("login-id")}    </h2>
        <div className="header-section">
          {cards.map((card) => (
            <Card key={card.card_no} className="card-item">
              <Card.Img variant="top" src={card.card_image} />
              <Card.Body>
                <Card.Title>{card.card_name}</Card.Title>
                <Card.Text>{card.benefit_main}</Card.Text>
              </Card.Body>
              <ListGroup className="list-group-flush">
                <ListGroup.Item> : {card.domestic_fee}</ListGroup.Item>
                <ListGroup.Item> : {card.overseas_fee}</ListGroup.Item>
                <ListGroup.Item> :<br />{card.benefit_cate}</ListGroup.Item>
              </ListGroup>
              <Card.Body>
                <Card.Link href={card.card_url}>   ?</Card.Link>
              </Card.Body>
            </Card>
          ))}
        </div><br/>
        <div className="footer-section">
          <Button variant="primary" className="button1" onClick={handleGoHome}>  </Button>
          <Button variant="primary" className="button2" onClick={handleShowModal}> </Button>
        </div><br/><br/>
          <Modal show={showModal} onHide={handleCloseModal}>
            <Modal.Header closeButton>
              <Modal.Title>  </Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <Star></Star>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="secondary" onClick={handleCloseModal}> </Button>
              <Button variant="primary" onClick={handleCloseModal}>  .</Button>
            </Modal.Footer>
          </Modal>
        </div>
    );
    }

export default Recommend;
